<template>
  <ul class="colors colors--black">
    <li
      class="colors__item"
      v-for="(col, index) in product.colors.length"
      :key="col.id"
    >
      <label class="colors__label">
        <input
          class="colors__radio sr-only"
          type="radio"
          v-model="currentProductColor"
          :value="index"
          @click.prevent="$emit('changeColor', index)"
        />
        <span
          class="colors__value"
          :style="{ backgroundColor: `${product.colors[index].color.code}` }"
        >
        </span>
      </label>
    </li>
  </ul>
</template>

<script>
export default {
  data() {
    return {
      currentProductColor: 0,
    };
  },
  props: ["product"],
};
</script>



