<template>
  <li class="catalog__item">
    <router-link
      class="catalog__pic"
      :to="{ name: 'product', params: { id: product.id } }"
    >
      <img
        v-if="product.colors[currentColor].gallery"
        :src="product.colors[currentColor].gallery[0].file.url"
        :alt="product.title"
      />
      <span v-else>Нет изображения</span>
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price"> {{ product.price | numberFormat }} ₽ </span>
    <ProductColor
      :product="product"
      @changeColor="(index) => changeColor(index)"
    />
  </li>
</template>

<script>
import ProductColor from "@/components/ProductColor.vue";
import gotoPage from "@/helpers/gotoPage";
import numberFormat from "@/helpers/numberFormat";

export default {
  data() {
    return {
      currentColor: 0,
      productColor: 0,
    };
  },
  methods: {
    gotoPage,
    changeColor(currentColor, productColor) {
      this.currentColor = currentColor;
      this.productColor = productColor;
    },
  },
  components: { ProductColor },
  props: ["product", "colors"],
  filters: {
    numberFormat,
  },
};
</script>

<style>
.catalog__item {
  padding: 10px;
  box-shadow: 0 0 8px 0 #e0e0e0;
}

.catalog__item:hover {
  padding: 10px;
  box-shadow: 0 0 12px 0 #252525;
}

.catalog__pic {
  width: 250px;
}
</style>