<template>
  <main class="content container" v-if="productsLoading">
    <img src="Hourglass.gif" alt="Загрузка товара" />
  </main>
  <main class="content container" v-else-if="!productData">
    Произошла ошибка при загрузке товара
  </main>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            {{ category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> {{ product.title }} </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper" v-if="product.colors[currentColor].gallery">
          <img
            width="570"
            height="570"
            :src="product.colors[currentColor].gallery[0].file.url"
            :alt="product.title"
          />
          <div class="flex">
            <div
              class="img__product mr cursor"
              v-for="(image, index) in product.colors.length"
              :key="index"
            >
              <div class="img_m" v-if="product.colors[index].gallery">
                <img
                  width="100"
                  height="100"
                  :src="product.colors[index].gallery[0].file.url"
                  :alt="product.title"
                  @click="changeColor(index, product.colors[index].color.id)"
                />
              </div>
            </div>
          </div>
        </div>
        <span v-else>Нет изображения</span>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ product.id }}</span>
        <h2 class="item__title">{{ product.title }}</h2>
        <div class="item__form">
          <form
            class="form"
            action="#"
            method="POST"
            @submit.prevent="addToCart"
          >
            <div class="item__row item__row--center">
              <div class="form__counter">
                <button
                  type="button"
                  aria-label="Убрать один товар"
                  @click="productRemove"
                >
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" v-model.number="productAmount" />

                <button
                  type="button"
                  aria-label="Добавить один товар"
                  @click="productAppend"
                >
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <b class="item__price">
                {{ (productAmount * product.price) | numberFormat }} ₽
              </b>
            </div>

            <div class="item__row">
              <fieldset class="form__block">
                <legend class="form__legend">Цвет</legend>
                <ul class="colors">
                  <ProductColor
                    :product="product"
                    @changeColor="(index) => changeColor(index)"
                  />
                </ul>
              </fieldset>

              <fieldset class="form__block">
                <legend class="form__legend">Размер</legend>
                <label
                  class="form__label form__label--small form__label--select"
                >
                  <select
                    class="form__select"
                    type="text"
                    name="category"
                    v-model="productSize"
                  >
                    <option
                      v-for="(size, index) in product.sizes.length"
                      :key="size.id"
                      :value="product.sizes[index]"
                    >
                      {{ product.sizes[index].title }}
                    </option>
                  </select>
                </label>
              </fieldset>
            </div>

            <button
              class="item__button button button--primery"
              type="submit"
              :disabled="productAddSending || productAmount < 1"
            >
              В корзину
            </button>
            <div v-show="productAdded">Товар добавлен в корзину</div>
            <div v-show="productAddSending">Добавляем товар в корзину...</div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current"> Информация о товаре </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#"> Доставка и возврат </a>
          </li>
        </ul>

        <div class="item__content">
          <h3>Состав:</h3>

          <p>
            60% хлопок<br />
            30% полиэстер<br />
          </p>

          <h3>Уход:</h3>

          <p>
            Машинная стирка при макс. 30ºC короткий отжим<br />
            Гладить при макс. 110ºC<br />
            Не использовать машинную сушку<br />
            Отбеливать запрещено<br />
            Не подвергать химчистке<br />
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
// import gotoPage from "@/helpers/gotoPage";
import numberFormat from "@/helpers/numberFormat";
import ProductColor from "@/components/ProductColor.vue";
import axios from "axios";
import { API_BASE_URL } from "@/config";
import { mapActions } from "vuex";

export default {
  data() {
    return {
      productAmount: 1,

      productData: null,

      productsLoading: false,
      productsLoadingFailed: false,

      productAdded: false,
      productAddSending: false,

      currentColor: 0,
      productColor: 0,
      productSize: null,
    };
  },
  components: { ProductColor },
  // props: ["colors"],
  filters: {
    numberFormat,
  },
  computed: {
    product() {
      return this.productData;
    },
    category() {
      return this.productData.category;
    },
  },
  methods: {
    changeColor(currentColor) {
      this.currentColor = currentColor;
      this.productColor = this.productData.colors[currentColor].color.id;
    },
    ...mapActions(["addProductToCart"]),
    // gotoPage,
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      this.addProductToCart({
        productId: this.product.id,
        colorId: this.productColor,
        sizeId: this.productSize.id,
        amount: this.productAmount,
      }).then(() => {
        this.productAdded = true;
        this.productAddSending = false;
      });
    },
    productAppend() {
      return (this.productAmount = this.productAmount + 1);
    },
    productRemove() {
      if (this.productAmount > 0) {
        return (this.productAmount = this.productAmount - 1);
      }
    },
    loadProduct() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      axios
        .get(API_BASE_URL + "/api/products/" + this.$route.params.id)
        .then((response) => (this.productData = response.data))
        .then(() => (this.productColor = this.productData.colors[0].color.id))
        .then(() => (this.productSize = this.productData.sizes[0]))
        .catch(() => (this.productsLoadingFailed = true))
        .then(() => (this.productsLoading = false));
    },
  },
  watch: {
    "$router.params.id": {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
};
</script>

<style>
.flex {
  display: flex;
}
.mr:not(:last-child) {
  margin-right: 10px;
}
.colors {
  --border-color: green;
}
.colors__radio:checked ~ .colors__value::before {
  border-color: black;
}

.cursor,
.form__select {
  cursor: pointer;
}
</style>